body {
	background:#fff;
}

a.border.nav-link.border-light.rounded.mr-1 {
  color: #3F729B;
}

a.border.nav-link.border-light.rounded.mr-1:hover {
  color: #1C2331;
}

.btn.btn-sm.indigo:hover{
  color: #fff;
}

.nav-link.active{
  background-color: rgba(255, 255, 255, 0.1);
}

#categories > .col-md-4.mb-5{
  margin-bottom: 8rem !important;
}
.sectionPage {
  position: relative;
  top: -15px;
  margin-bottom: -125px;
}

.home-feature-box .fa {
	font-size:6rem;
}

.home-feature-box span {
	display: block;
	color:#111;
	font-weight:bold;
	margin-top:1.5rem;
}

.example-components-list li > a {
  color: #495057;
}

.example-components-list li:last-child > a {
	border-bottom:0;
}

.example-components-list li > a .fa {
	color:rgba(0,0,0,.35);
	float:right;
}

//> Footer
// Pulsating heart
footer .pulse {
  -webkit-animation: pulse 1s infinite;
  animation: pulse 1s infinite;
}
.pulse {
  -webkit-animation-name: pulse;
  animation-name: pulse;
}

//> Floating in eternal space
// Support Card
.support-card{
  position: fixed;
  top: 2rem;
  right: 2rem;
}
@media screen and (max-width: 600px) {
  .support-card{
    display: none;
  }
}

//> Chart Card
.chart-card{
  box-shadow: none;
}

//> Coach Card
.coach-card{
  img{
    height: 100px;
  }
}

//> Timeline Card
.timeline-card{
  box-shadow: none;
  font-weight: 300;
  max-width: 600px;
}

